import React from 'react';
import "../Components/login.css"
import { useState } from 'react'
import { Redirect } from 'react-router';
const Login = () => {
  let admin = {
    user: "test-admin",
    password: "test-admin",
  };
  let salesExecutive = {
    First_name: "test-sales",
    Password: "test-sales",
    Avatar: "fas fa-user-tie",
    DOB: "26/07/1995",
    Exp: "5",
    Gender: "Male",
    Second_name: "-",
    id: 1631540396845,
    img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"
  }
  let incoming;
  const andy = () => {
    if (JSON.parse(localStorage.getItem("TeamStock")))
      incoming = JSON.parse(localStorage.getItem("TeamStock"));
    else {
      let sales_executive = [];
      sales_executive.push(salesExecutive);
      localStorage.setItem(
        "TeamStock",
        JSON.stringify(sales_executive)
      );
    }
  }
  andy();
  console.log(incoming);
  const handleStoreManager = () => {
    let y = document.getElementById("managerlogin").value;
    let y1 = document.getElementById("managerpassword").value;
    console.log(y, y1);
    if (y === "" || y1 === "") alert("please fill all the fields");
    else {
      if (y.toLowerCase() === admin.user && y1 === admin.password)
        setstore(true);
      else alert("Either username or password is wrong");
    }
  };
  const handleSalesExecutive = () => {
    console.log(incoming)
    let yx =0;
    let y = document.getElementById("exelogin").value.toLowerCase() ;
    let y1 = document.getElementById("exepassword").value;
    if (y == "" || y1 == "") alert("please fill all the fields");
    else {
      for (let i = 0; i < incoming.length; i++) {
        console.log("ch", incoming[i].First_name, y, incoming[i].Password, y1)
        if (incoming[i].First_name.toLowerCase() == y && y1 == incoming[i].Password) {
          yx=1;setsales(true);
          let linky = '/sales' +'?'+ 'id=' +y;
          setuser(linky)
          break;
        } else {
          yx=0
          ;
        }
      }
      if(yx===0)
      alert("Either username or password is wrong")
    }
  };
  const [store, setstore] = useState(false);
  const [sales, setsales] = useState(false);
  const [user, setuser] = useState("")
  return (
    <div id="mainlogin">
      {store && <Redirect to="/storemanager" />}
      {sales && <Redirect to={user} />}
      <div>
        <div className="wrapper fadeInDown">
          <div id="formContent">

            <h2 className="active" id="head"> Manager Login </h2>


            <div className="fadeIn first">
              <img src="https://mpng.subpng.com/20180402/otw/kisspng-project-management-information-technology-computer-manager-5ac2ee961ab602.4831266015227245021094.jpg" id="icon" alt="User Icon" />
            </div>

            <form>
              <input type="text" id="managerlogin" className="fadeIn second" name="login" placeholder="login" />
              <input type="text" id="managerpassword" className="fadeIn third" name="login" placeholder="password" />
              <button id="buttoning" onClick={() => handleStoreManager()}>login</button>
            </form>


          </div>
        </div>

      </div>

      <div>
        <div class="wrapper fadeInDown">
          <div id="formContent">

            <h2 class="active" id='head'>Executive Login</h2>


            <div class="fadeIn first">
              <img src="https://mpng.subpng.com/20180402/otw/kisspng-project-management-information-technology-computer-manager-5ac2ee961ab602.4831266015227245021094.jpg" id="icon" alt="User Icon" />
            </div>

            <form>
              <input type="text" id="exelogin" class="fadeIn second" name="login" placeholder="login" />
              <input type="text" id="exepassword" class="fadeIn third" name="login" placeholder="password" />
              <button id="buttoning" onClick={() => handleSalesExecutive()}>login</button>
            </form>


          </div>
        </div>



      </div>

    </div>

  );

}

export default Login;